    margin: 0 6rpx;
  }
  .total {
    font-size: 24rpx;
    color: $hubei-text-color-sub;
  }
  .total-num {
    color: $hubei-primary;
  }
  .body {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 24rpx;
    margin-top: 32rpx;
  }
  .month-button {
    border-radius: 50%;
    width: 86rpx;
    height: 86rpx;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 28rpx;
    background-color: #fff;
    color: $hubei-text-color-sub;
    margin: 0 auto;
    &.active {
      background-color: $hubei-primary;
      color: #fff;
      .month-number {
        color: #fff;
      }
    }
  }
  .month-number {
    font-size: 32rpx;
    font-weight: 500;
    color: $hubei-text-color-primary;
  }
}
</style>
